<template lang="html">
  <section class="container-fluid">

    <div :class="{ 'col-md-12' : !selected, 'col-md-8' : selected}">
      <article class="panel panel-primary">
        <div class="panel-heading">
          Graph Custom SVG + VueJs
        </div>
        <div class="panel-body">
          <active-tree v-model="treeData" :handleSelect="select"></active-tree>
        </div>
      </article>
    </div>

    <div class="col-md-4"  v-if="selected">
      <article class="panel panel-success">
        <div class="panel-heading">
          {{selected.name}}
        </div>
        <div class="panel-body">
          <input type="text" v-model="selected.name">
        </div>
      </article>
    </div>

  </section>
</template>

<script>

import ActiveTree from '@/common/ActiveTree'

export default {
  name: 'graph-custom',
  components: {
    ActiveTree
  },
  data () {
    return {
      treeData: {
        "name": "REF_1",
        "children": [
          { "name": "REF_1_1" },
          {
            "name": "REF_1_2",
            "children": [
              { "name": "REF_1_2_1" },
              {
                "name": "REF_1_2_2",
                children: [
                  { "name": "REF_1_2_2_1" },
                  { "name": "REF_1_2_2_2" },
                  { "name": "REF_1_2_2_3" }
                ]
              },
              { "name": "REF_1_2_3" }
            ]
          },
          {
            "name": "REF_1_3",
            children: [
              {
                name: 'REF_1_3_1',
                children: [
                  { "name": "REF_1_3_1_1" },
                  { "name": "REF_1_3_1_2" },
                  { "name": "REF_1_3_1_3" }
                ]
              }
            ]
          }
        ]
      },
      selected: null
    }
  }, methods : {
    select(node) {
      this.selected = node
    }
  }
}
</script>

<style lang="css">
</style>



